:root {
  font-size: 62.5%;
}

html,
body {
  padding: 0;
  margin: 0;
  width: 100%;
  min-height: 100vh;
  -webkit-text-size-adjust: none;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat-Regular", sans-serif;
  overflow-x: hidden;
  background-color: rgba(255, 233, 118, 0.43);
}

::-webkit-scrollbar {
  width: 6px;
  min-height: 12px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(143, 31, 232, 0.59);
}

::-webkit-scrollbar-corner {
  border-radius: 0;
}

footer {
  margin-top: auto;
}

button {
  cursor: pointer;
}

input[type=text], textarea {
  -webkit-appearance: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

input, textarea {
  border-radius: 0;
}

/*svg {
  transition: fill $transition_rules;
  fill: #97A2AC;
}*/
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.cmn__wrapper {
  max-width: 1920px;
  min-width: 320px;
  padding: 0 12rem;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.header__top {
  min-height: 8rem;
  background: rgba(243, 225, 130, 0.57);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
}
.header__main {
  background: linear-gradient(180deg, rgba(214, 193, 85, 0.09) 0%, rgba(165, 142, 24, 0.36) 100%);
  background-blend-mode: darken;
}
.header__main-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 10rem;
}
.header__nav {
  display: flex;
  align-items: center;
}
.header__nav-item {
  margin-right: 2.5rem;
}
.header__nav-item:nth-child(1n+4) {
  display: none;
}
.header__nav-item a {
  font-size: 2.6rem;
  line-height: 3.2rem;
  text-transform: capitalize;
  color: #000000;
}
.header__action {
  display: flex;
  align-items: center;
}
.header__action-item {
  width: 5rem;
  height: 5rem;
  margin: 0 1rem;
}
.header__action-item a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.crawl-line {
  height: 3.5rem;
  background-color: rgba(143, 31, 232, 0.59);
}
.crawl-line p {
  font-family: "Montserrat-SemiBold";
  font-size: 2.4rem;
  line-height: 2.9rem;
  text-align: center;
  color: #ffffff;
}

.nav {
  background: linear-gradient(180deg, rgba(165, 142, 24, 0.36) 14.37%, rgba(214, 193, 85, 0.11) 82.08%);
}
.nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.nav__item {
  flex-grow: 1;
}
.nav__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0.5rem;
  overflow-x: hidden;
}
.nav__link span {
  position: relative;
  font-family: "HeinrichScript";
  font-size: 3.6rem;
  line-height: 3.7rem;
  text-transform: capitalize;
  color: #000000;
  transition: color 0.4s 0.2s ease;
}
.nav__link span::before {
  position: absolute;
  left: 50%;
  bottom: -0.5rem;
  transform: translateX(-50%);
  content: "";
  width: 100%;
  height: 3px;
  opacity: 0;
  border-bottom: 3px solid #a58e18;
  transition: opacity 0.4s 0.2s ease;
}
.nav__link:hover span, .nav__link:focus span {
  color: rgba(143, 31, 232, 0.59);
}
.nav__link:hover span::before, .nav__link:focus span::before {
  opacity: 1;
}